/* ### Masonry */

.blog,
.archive,
.page-template-page_blog {
	.site-inner {
		background-color: $white;
	}
	.content {
		display: flex;
		flex-wrap: wrap;
	}
	&.content-sidebar,
	&.sidebar-content {
		.entry {
			@include breakpoint(md) {
				width: $one-third-blog;
			}
		}
	}
	.entry {
		width: 100%;
		padding: 3rem;
		margin-bottom: $margin-bottom-blog;
		box-shadow: 0 1rem 2rem -1rem rgba($dark,0.1);
		background-color: $white;
		border-radius: $site-radius;
        @include transition;
		@include breakpoint(xs) {
			width: $one-half-blog;
			&:nth-of-type(even) {
				margin-left: $margin-left-blog;
			}
		}
		@include breakpoint(md) {
			width: $blog-columns;
			margin-left: $margin-left-blog;
			&:first-of-type {
				margin-left: 0;
			}
			@include first-of-row();
		}
        &:hover {
            box-shadow: 0 2rem 1rem -1rem rgba($dark,0.1);
        }
	}
	.entry-title {
		font-size: 1.6rem;
	}
	.entry-content {
		font-size: 1.8rem;
		color: $gray;
	}
	.entry-footer {
		margin-top: 1rem;
		p {
			margin: 0;
		}
	}
	.entry-image-link {
		display: block;
		margin: -3rem -3rem 3rem;
		.single & {
			@include breakpoint(sm) {
				margin: -6rem -6rem 6rem;
			}
		}
		img {
			border-radius: $site-radius $site-radius 0 0;
		}
	}
}

